import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useQuery } from 'react-query';
import { fetchRoomList, checkRoomPassword } from '../services/api.js';

const useEntrance = () => {
  const [roomName, setRoomName] = useState('');
  const [roomPassword, setRoomPassword] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [tempRoomName, setTempRoomName] = useState('');
  const navigate = useNavigate();

  const { data: roomList, isLoading } = useQuery('roomList', fetchRoomList);

  const makeNewRoom = async () => {
    if (!roomName || !roomPassword) {
      alert(!roomName ? '   .' : '  .');
      return;
    }
    navigate('/room', { state: { roomName, roomPassword } });
  };

  const chooseRoom = (roomName) => { //   
    setTempRoomName(roomName);
    setModalIsOpen(true);
  };

  const handleRoomJoin = async () => { //   
    try {
      const verifyPassword = await checkRoomPassword(tempRoomName, roomPassword);
      if (verifyPassword) {
        setModalIsOpen(false);
        navigate('/room', { state: { roomName: tempRoomName, roomPassword } });
      } else {
        alert(' .');
        setRoomPassword('');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('  .');
    }
  };

  const closeButton = () => {
    setModalIsOpen(false);
    setRoomPassword('');
  };

  return {
    roomName,
    setRoomName,
    roomPassword,
    setRoomPassword,
    modalIsOpen,
    setModalIsOpen,
    tempRoomName,
    setTempRoomName,
    navigate,
    roomList,
    isLoading,
    makeNewRoom,
    chooseRoom,
    handleRoomJoin,
    closeButton
  };
};

export {useEntrance};